:root {
  --purple: hsl(256, 26%, 20%);
  --grayish-blue: hsl(216, 30%, 68%);
  --dark-purple: hsl(270, 9%, 17%);
  --gray: hsl(0, 0%, 98%);
  --white: hsl(0, 0%, 100%);
  --black: hsl(0, 0%, 0%);
}

html {
  font-size: 1rem;
  font-family: "Karla";
}

body {
  background-color: var(--white);
}

h1 {
  font-size: 2rem;
  font-weight: 400;
  font-family: "DM Serif Display";
}

h2 {
  font-size: 1.7rem;
  font-weight: 400;
  font-family: "DM Serif Display";
}

h3 {
  font-size: 1.6rem;
  font-weight: 400;
  font-family: "DM Serif Display";
}

h4 {
  font-size: 1.4rem;
  font-weight: 400;
  font-family: "DM Serif Display";
}